*{
    margin: 0;
    padding: 0;
    border: 0;
    outline: 0;
    box-sizing: border-box;
    list-style: none;
    text-decoration: none;
}


:root {
    /* colors variables */
    --bg-color: #1f1f38;
    --bg-color-variant: #2c2c6c;
    --primery: #00ffff; 
    --primery-vairiant: #00ffff8c;
    --white: #fff;
    --black: #000;
    --light: rgba(255,255,265, .6);

    /* basic variables */

    --container-lg: 75%;
    --container-md: 86%;
    --container-sm: 90%;
}
html{
    scroll-behavior: smooth;
}
